import React, { useState } from "react";
import {
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
} from "mdb-react-ui-kit";
import {
  Checkbox,
  FormControl,
  FormControlLabel,
  InputLabel,
  Select,
  MenuItem,
  Slider,
  Switch,
  TextField,
  
} from "@mui/material";


function AcademicProfile(){
    const [age, setAge] = React.useState("");
  const [satScore, setSatScore] = useState(false);

  const handleChange = (event) => {
    setAge(event.target.value);
  };

    return(
        <div>
            <MDBCard>
          <MDBCardBody className="px-4">
            <h3 className="fw-bold mb-4 pb-2 pb-md-0 mb-md-5">
              Academic Profile
            </h3>
            <MDBRow>
            <MDBCol md="8">
                <FormControl fullWidth>
                  <InputLabel id="high-school-graduation-completion-year-select-label">
                  Name of High School
                  </InputLabel>
                  <Select
                    labelId="high-school-graduation-completion-year-select-label"
                    id="high-school-graduation-completion-year-select"
                    label="high school graduation completion year"
                  >
                    <MenuItem value={10}>school 1</MenuItem>
                    <MenuItem value={20}>school 2 </MenuItem>
                    <MenuItem value={30}>school 3</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="high-school-graduation-completion-year-select-label">
                  High School Graduation (12th grade)/completion year
                  </InputLabel>
                  <Select
                    labelId="high-school-graduation-completion-year-select-label"
                    id="high-school-graduation-completion-year-select"
                    label="high school graduation completion year"
                  >
                    <MenuItem value={10}>2022</MenuItem>
                    <MenuItem value={20}>2023</MenuItem>
                    <MenuItem value={30}>2024</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
             
            </MDBRow>
            <br />
            <MDBRow>
            <MDBCol md="4">
                <TextField
                  id="standard-basic"
                  type="number"
                  label="Looking to Enroll in"
                  variant="standard"
                  fullWidth
                />
              </MDBCol>
           
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                    ENROLLED AS
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="ENROLLED AS"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>FIRST YEAR</MenuItem>
                    <MenuItem value={20}>TRANSFER</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                    Transfer Year
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="Transfer year"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>Sophmore</MenuItem>
                    <MenuItem value={20}>Junior</MenuItem>
                    <MenuItem value={30}>Senior</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
            </MDBRow>
            <br />

            <MDBRow>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                    Student Class Rank (percentile)
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="type"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>Top 10%</MenuItem>
                    <MenuItem value={20}>Top Quarter</MenuItem>
                    <MenuItem value={30}>Top half</MenuItem>
                    <MenuItem value={40}>Bottom Half</MenuItem>
                    <MenuItem value={50}>Bottom Quarter</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <TextField
                  id="standard-basic"
                  type="number"
                  label="Student GPA"
                  variant="standard"
                  fullWidth
                />
              </MDBCol>
            </MDBRow>
            <br />
            <MDBRow>
            <MDBCol md="4">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">
                    Transfer Year
                  </InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={age}
                    label="Transfer year"
                    onChange={handleChange}
                  >
                    <MenuItem value={10}>Certificate</MenuItem>
                    <MenuItem value={20}>Associate Degree</MenuItem>
                    <MenuItem value={30}>Bachelor's Degree</MenuItem>
                  </Select>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <FormControl fullWidth>
                  <p>
                    International Baccalauerate (IB)
                    <Switch labelId="demo-simple-select-label" />
                  </p>
                </FormControl>
              </MDBCol>
              <MDBCol md="4">
                <TextField
                  id="standard-basic"
                  type="number"
                  label="Predicted IB Score (Out of 45)"
                  variant="standard"
                  fullWidth
                />
              </MDBCol>
            </MDBRow>
            <br />
            <MDBRow>
              <MDBCol md="2">
                <FormControlLabel
                  control={<Checkbox />}
                  label="PSAT SCORE "
                  labelPlacement="end"
                  id="psat_score_enable"
                />
              </MDBCol>
              <MDBCol md="4">
                <p>
                  PSAT Composite :
                  <Slider
                    max={"760"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="2">
                <FormControlLabel
                  control={<Checkbox />}
                  label="SAT SCORE "
                  labelPlacement="end"
                  id="sat_score_enable"
                />
              </MDBCol>
              <MDBCol md="4">
                <p>
                  SAT Composite :
                  <Slider
                    max={"760"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="3">
                <p>
                  PSAT VERBAL :
                  <Slider
                    max={"760"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  PSAT MATHS :
                  <Slider
                    max={"760"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  SAT VERBAL :
                  <Slider
                    max={"760"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  SAT MATHS :
                  <Slider
                    max={"760"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
            </MDBRow>
            <MDBRow>
            <MDBCol md="2">
                <FormControlLabel
                  control={<Checkbox />}
                  label="ACT SCORE "
                  labelPlacement="end"
                  id="psat_score_enable"
                />
              </MDBCol>
              <MDBCol md="4">
                <p>
                  Act Composite :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBRow>
              <MDBCol md="3">
                <p>
                  ACT VERBAL :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  ACT MATHS :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  ACT ENGLISH :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
              <MDBCol md="3">
                <p>
                  ACT SCIENCE :
                  <Slider
                    max={"36"}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                  />
                </p>
              </MDBCol>
            </MDBRow>
            </MDBRow>
          </MDBCardBody>
        </MDBCard>
        </div>
    );
}
export default AcademicProfile;