import * as React from "react";
import PropTypes from "prop-types";
import SwipeableViews from "react-swipeable-views";
import { useTheme } from "@mui/material/styles";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import DemographicInformation from "./RegisterComponents/DemographicInformation";
import AcademicProfile from "./RegisterComponents/AcademicProfile";
import FinancialInformation from "./RegisterComponents/FinancialInformation";
import PrefrencesMotivation from "./RegisterComponents/PrefrencesMotivation";
import Login from "../Authentication/Login";
import { BottomNavigation, BottomNavigationAction, Paper } from "@mui/material";
import { style } from "@mui/system";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    "aria-controls": `full-width-tabpanel-${index}`,
  };
}

export default function FormBody() {
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const handleChangeIndex = (index) => {
    setValue(index);
  };

  return (
    <div>
      <Box sx={{ bgcolor: "background.paper", width: "100%"}} style={{marginBottom: '50px'}}>
        <SwipeableViews
          axis={theme.direction === "rtl" ? "x-reverse" : "x"}
          index={value}
          onChangeIndex={handleChangeIndex}
        >
          <TabPanel value={value} index={0} dir={theme.direction}>
            <DemographicInformation />
          </TabPanel>
          <TabPanel value={value} index={1} dir={theme.direction}>
            <AcademicProfile />
          </TabPanel>
          <TabPanel value={value} index={2} dir={theme.direction}>
            <FinancialInformation />
          </TabPanel>
          <TabPanel value={value} index={3} dir={theme.direction}>
            <PrefrencesMotivation />
          </TabPanel>
        </SwipeableViews>
      </Box>
      <Paper
        sx={{ position: "fixed", bottom: 0, left: 0, right: 0 }}
        elevation={3}
      >
        <BottomNavigation
          showLabels
          value={value}
          onChange={(event, newValue) => {
            setValue(newValue);
          }}
        >
          <BottomNavigationAction label="DemoGraphic Information" />
          <BottomNavigationAction label="Academic Profile" />
          <BottomNavigationAction label="Financial Information" />
          <BottomNavigationAction label="Preferences Motivation" />
        </BottomNavigation>
      </Paper>
    </div>
  );
}
